
* {
  margin: 0px;
}

nav {
  height: 100px;
  display: flex;
 justify-content: space-evenly;
align-items: center;
}

.navbar-img {
   width: 160px;

}

.Searchbar-container { 
  display: flex;
  margin: 0px auto;
  padding: 0 20px;
  align-items: center;
 
}

.searchbar {
    background-color: white;
    margin-right: 20px;

}

.searchbar input {
  padding: 10px;
  box-shadow: 0px 2px 2px(0,0,0,0.25);
  margin-right: 20px;
 border-radius: 20px;
  border: none;
}

.searchBar-Btn button {
      background-color: #0e6f9f;
      border: none;
     border-radius: 10px;
     height: 44px;
     color: white;
     padding: 10px 12px;

}

.pokedex-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
   padding: 10px;
}

.pokedex-grid {
  display: grid;
  gap: 10px;
  grid-template-columns: repeat(3,1fr);
}

p {
  color: red;
  font-weight: bold;
  
}
.pokemon-heart-btn {
  height: 20px;
  max-width: 50px;
}
.pokemon-card {
  display: flex;
  box-shadow: 4px 4px 4px (0,0,0,0.25);
  border-radius: 3px;
}

.pokemon-image {
  width: 70px;
  height: 70px;
  margin-right: 10px;
}


.pokemon-img-container {
  padding: 0px 10px;
}

.card-body {
 display: flex;
 flex-direction: column;
 justify-content: space-between;
 padding: 10px 10px 10px 0px;
 flex: 1;
}

.card-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.card-top h3 {
  text-transform: capitalize;

  
}
.card-bottom {
   display: flex;
   align-items: center;
   justify-content: space-between;
}

.pokemon-type   {
  display: flex;
 
}
.pokemon-type-text   {
  margin-right: 10px;
  text-transform: capitalize;
 
}
.pagination-container {
  display: flex;
   align-items: center;
  

}